import React, { useState } from 'react';
import './Loginsingup.css';

import user_icon from './person.png';
import email_icon from './email.png';
import password_icon from './password.png';


const Loginsingup = () => {

const [action, setAction] = useState("");
const [nameValue, setNameValue] = useState("");
const [emailValue, setEmailValue] = useState("");
const [passwordValue, setPasswordValue] = useState("");

const handleInputChange = (e, field) => {
	const value = e.target.value;
	switch (field){
		case "name":
			setNameValue(value);
			break;
		case "email":
			setEmailValue(value);
			break;
		case "password":
			setPasswordValue(value);
			break;
		default:
			break;
	}
}

return (
	<div className='container'>
	  <div className="header">
		<div className="text">{action}</div>
		<div className="underline"></div>
	  </div>
	  <div className="inputs">
	  	{action===""?<div></div>: <div className="input">
			<img src={user_icon} alt=''/>
			<input type='text' 
			placeholder=''
			value={nameValue}
			onChange={(e) => handleInputChange(e, "name")}
			/>
		</div>}
		<div className="input">
			<img src={email_icon} alt=''/>
			<input type='email' 
			placeholder='' 
			value={emailValue}
			onChange={(e) => handleInputChange(e, "email")}
			/>
		</div>
		<div className="input">
			<img src={password_icon} alt=""/>
			<input type='password' 
			placeholder=''
			value={passwordValue}
			onChange={(e) => handleInputChange(e, "password")}
			/>
		</div>
	  </div>
	  
	  {action===""?<div></div>: <div className="forgot-password">  ?<span> !</span></div>}
	  	<div className="submit-container">
	  		<div className={action === "" ? "submit gray" : "submit"} onClick={()=>{setAction("");setNameValue(""); setEmailValue("");setPasswordValue("");}}></div>
			<div className={action === "" ? "submit gray" : "submit"} onClick={()=>{setAction("");setEmailValue("");setPasswordValue("");}}></div>
		</div>
	</div>
  )
}

export default Loginsingup;